import React, { useState, useEffect } from "react";
import classNames from "classnames";
import styles from "./ReactSheet.module.css";

// ReactSheetDialog component definition
function ReactSheetDialog(props) {
  // State to manage the open state of the sheet/dialog
  const [open, setOpen] = useState(false);

  // Destructure modal and dialogType properties from props
  const { modal, dialogType } = props;

  // Function to open the sheet/dialog
  const openSheet = () => {
    setOpen(true);
  };

  // Function to close the sheet/dialog
  const closeSheet = () => {
    setOpen(false);
  };

  // ...

  // Check if the dialogType is a standard dialog
  const isStandardDialog = dialogType === "standard";

  // Set CSS classes based on the open state and dialogType
  const sheetClass = open ? styles.open : "";
  const overlayClass = open && modal;

  // Update styles conditionally based on dialogType using classNames
  const sheetStyle = classNames(styles.sheet, sheetClass, {
    [styles.standardDialog]: isStandardDialog,
  });

  return (
    <>
      {/* Trigger button to open the sheet/dialog */}
      <button
        onClick={openSheet}
        id="side-sheet-button"
        className={styles.sideSheetButton}
      >
        Open Sheet
      </button>
      {/* Overlay displayed when the sheet/dialog is open and modal */}
      {overlayClass && (
        <div className={styles.overlay} onClick={closeSheet}></div>
      )}
      {/* Sheet/Dialog element */}
      <div
        className={sheetStyle}
        role="dialog"
        aria-labelledby="sheet-heading"
        {...(modal ? { "aria-modal": true } : {})}
        aria-hidden={!open}
        onKeyDown={(event) => {
          trapFocus(event);
        }}
      >
        {/* Sheet/Dialog header */}
        <div className={styles.sheetHeader}>
          {/* Heading */}
          <h2 id="sheet-heading" className={styles.sheetHeading} tabIndex="-1">
            Sheet Dialog
          </h2>

          {/* Close button */}
          <button className={styles.closeButton} onClick={closeSheet}>
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="var(--purple-900)"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M18.3 5.71a.996.996 0 00-1.41 0L12 10.59 7.11 5.7A.996.996 0 105.7 7.11L10.59 12 5.7 16.89a.996.996 0 101.41 1.41L12 13.41l4.89 4.89a.996.996 0 101.41-1.41L13.41 12l4.89-4.89c.38-.38.38-1.02 0-1.4z" />
            </svg>
          </button>
        </div>
        {/* Sheet/Dialog content */}
        <div className={styles.sheetContent}>
          <h3>Control Props</h3>
          <p>
            This component has two control props: <code>modal</code> and{" "}
            <code>dialogType</code>.
          </p>
          <ul>
            <li>
              <code>modal</code>: Determines if the sheet/dialog
            </li>
            <li>
              <code>dialogType</code>: Defines the type of dialog to display,
              either "sheet" or "standard". It accepts a string value (default:
              "sheet").
            </li>
          </ul>
          <h3>When to Use Sheet and Standard Dialogs</h3>
          <p>
            Both sheet and standard dialogs can be used to display additional
            content, prompt user input, or present options in a separate layer.
            However, they serve different purposes based on the context and user
            experience you want to achieve.
          </p>
          <h4>Sheet Dialog</h4>
          <p>
            Sheet dialogs are ideal for displaying content or actions related to
            the current context or page. They usually slide in from the edge of
            the screen and cover only a portion of the content, allowing the
            user to maintain a connection with the underlying page. Sheet
            dialogs are typically used for navigation menus, filters, or
            contextual actions on larger screens.
          </p>
          <h4>Standard Dialog</h4>
          <p>
            Standard dialogs are used to display more critical information,
            prompt user input, or present actions that require the user's full
            attention. They usually appear in the center of the screen, with a
            backdrop that prevents interaction with the content underneath. Use
            standard dialogs for confirmation dialogs, user input forms, or when
            the user must complete an action before proceeding.
          </p>
        </div>
      </div>
    </>
  );
}

export default ReactSheetDialog;
